import * as React from "react"
import { useState } from "react"
import { HeadFC, PageProps, graphql, useStaticQuery } from "gatsby"
import { breakNumber, numberToStar, numberMeaning, stars, combo, alphaToNumber } from '../helpers/NumberEnergy'
import { Trans, useTranslation } from 'gatsby-plugin-react-i18next';


const IndexPage: React.FC<PageProps> = () => {

  const { t } = useTranslation();
  const [query, setQuery] = useState('')
  const [numberChunk, setNumberChunk] = useState([])
  const [moreClause, setMoreClause] = useState([])

  const analyseNumber = (query) => {
    const result = []
    const chunk = breakNumber(query);
    const lastNumberPair = chunk[chunk.length - 1];

    const starRepresentation = chunk.map(e => numberToStar(e).type)


    const numOfStar = stars.map(star => {
      return {
        star,
        count: starRepresentation.filter(_star => _star === star).length
      }
    })

    if (query.includes('91')) {
      result.push('91 is not advisable to be used by women.')
    }

    if (query.includes('87')) {
      result.push('87 is not advisable to be used by ladies.')
    }

    if (query.includes('0')) {
      result.push('Avoid 0 in the number.')
    }

    if (['wugui', 'liusha', 'huohai', 'jueming'].includes(numberToStar(chunk[chunk.length - 1]).type)) {
      result.push(`Avoid ${lastNumberPair} at the end.`)
    }

    numOfStar.forEach(e => {
      if (e.count > 2) {
        result.push(`${e.count} ${t(e.star)} star in the number. Too many of a certain star may not be a good thing.`)
      }
    })




    return result;
  }

  const handleQuery = () => {
    const chunk = breakNumber(alphaToNumber(query))
    setNumberChunk(chunk)
    setMoreClause(analyseNumber(query))
  }

  React.useEffect(() => {
    const chunk = breakNumber(alphaToNumber(query))
    setNumberChunk(chunk)
    setMoreClause(analyseNumber(query))
  }, [query])

  const isGoodEnergy = (star) => ['tianyi', 'yannian', 'shengqi'].includes(star.trim())

  return (
    <div className="page-wrapper">
      <header className="page-header">
        <div className="page-header__scroll" data-uk-sticky>
          <div className="uk-container uk-container-xlarge">
            <div className="page-header__inner">
              <div className="page-header__left">
                <div className="page-header__logo logo"><a className="logo__link" href="/"><h2>OpenFengShui</h2></a></div>
              </div>
              <div className="page-header__right">
                <div className="page-header__btn"></div>
                <div className="page-header__btn-menu"><a href="#offcanvas" data-uk-toggle data-uk-icon="menu"></a></div>
              </div>
            </div>
          </div>
        </div>
      </header>
      <main className="page-main">
        <div className="section-banner">
          <div className="section-banner__bg" style={{ backgroundImage: "url(assets/img/bg-banner.jpg)" }} >
            <div className="uk-container uk-text-center">
              <div className="section-banner__content">
                <div className="section-banner__title">Is Your Number Lucky?</div>
                <div className="section-banner__text">Harness the power of ancient chinese wisdom. Anaylse Your Number For Free here.</div>
                <div className="section-banner__form">
                  <form action="#!">
                    <div className="form-search">
                      <div className="form-search__box">
                        <input autoFocus={true} type="text" value={query} placeholder="" onChange={(e) => setQuery(e.target.value)} />
                        <a className="uk-button uk-button-danger" href="https://www.facebook.com/openfengshui" type="submit" > { t("Facebook")} </a></div>
                    </div>
                  </form>
                </div>
                <div className="section-banner__bottom">
                  <a href="#disclaimer">Disclaimer</a>
                </div>
              </div>
            </div>
          </div>
        </div>
        {
          query && !!numberChunk.length && <div className="section-info">
            <div className="uk-section-large uk-container uk-container-large">
              <div className="uk-grid uk-child-width@m" data-uk-grid>
                <div className="uk-width-3-4@m ">
                  <div className="section-content">
                    <h2>Number Energy For {query} ({ alphaToNumber(query) }) </h2>
                    <ul className="info-list">

                      {
                        stars.map((star, i) => {
                          const starChunk = numberChunk.filter(num => numberToStar(num).type === star)
                            .map(numberToStar)
                          if (starChunk.length === 0) {
                            return null
                          }
                          return <li key={i}>
                            <div className="info-list-item">
                              <div className="info-list-item__desc">
                                <div className="info-list-item__text">
                                  <h5>{starChunk

                                    .map(star => { return `${t(star.type)} Star  Lvl ${star.level}` })
                                    .join(' => ')
                                  }
                                  </h5>
                                  <div>
                                    {t(`${star}_representation`)}
                                  </div>
                                </div>
                              </div>
                            </div>
                          </li>
                        })
                      }
                    </ul>
                  </div>
                </div>


              </div>
            </div>
          </div>
        }
        <div className="section-steps">
          <div className="uk-section-large uk-container">
            <div className="section-title uk-text-center"><span>{t('readDisclaimer')}</span>
              <h3 className="uk-h3"> {t("recommendation")}</h3>

              <div className="">

                {
                  moreClause.length === 0 && t('noRecommendation')
                }
                {
                  moreClause && moreClause.map((c, i) => <div key={i}>
                    {i + 1}）{c}
                    <br />
                    <br />
                  </div>)
                }
              </div>
            </div>
          </div>
        </div>

        <div className="section-info">
          <div className="uk-section-large uk-container">
            <div className="section-title uk-text-center"><span>{ t('readDisclaimer') } </span>
              <h3 className="uk-h3"> { t('starChart') } </h3>
              <div className="star-chart2">
                <div className='div1'>

                </div>
                <div className='div2'>
                  Level 1
                </div>
                <div className='div3'>
                  Level 2
                </div>
                <div className='div4'>
                  Level 3
                </div>
                <div className='div5'>
                  Level 4
                </div>
              </div>
              <div className="star-chart">
                {
                  stars.map((s, i) => <>
                    <div className={`div${i * 5 + 1} ${isGoodEnergy(s) && 'uk-color-red'}`}> {t(s)} </div>
                    <div className={`div${i * 5 + 2} ${isGoodEnergy(s) && 'uk-color-red'}`}> {combo[`${s}1`].join('/')} </div>
                    <div className={`div${i * 5 + 3} ${isGoodEnergy(s) && 'uk-color-red'}`}> {combo[`${s}2`].join('/')}</div>
                    <div className={`div${i * 5 + 4} ${isGoodEnergy(s) && 'uk-color-red'}`}> {combo[`${s}3`].join('/')}</div>
                    <div className={`div${i * 5 + 5} ${isGoodEnergy(s) && 'uk-color-red'}`}> {combo[`${s}4`].join('/')}</div>

                  </>)
                }
              </div>
            </div>
          </div>
        </div>
        <div className="section-steps" id="disclaimer">
          <div className="uk-section-large uk-container">
            <div className="section-title uk-text-center"><span> {t('dyor')}</span>
              <h3 className="uk-h3">Disclaimer</h3>
              <p>
                Under no circumstances OpenFengShui be held responsible or liable in any way for any claims, damages, losses, expenses, costs or liabilities whatsoever (including, without limitation, any direct or indirect damages for loss of profits, business interruption or loss of information) resulting or arising directly or indirectly from your use of or inability to use this website or any websites linked to it, or from your reliance on the information and material on this website, even if the OpenFengShui has been advised of the possibility of such damages in advance.
              </p>
            </div>
          </div>
        </div>
      </main>
      <footer className="page-footer">
        <div className="page-footer__bottom">
          <div className="uk-container"><span>© OpenFengShui. All rights reserved.</span>
          </div>
        </div>
      </footer>
    </div>
  )
}

export default IndexPage

export const Head: HeadFC = () => <title>Is Your Number Lucky?</title>


export const query = graphql`
  query($language: String!) {
    locales: allLocale(filter: {language: {eq: $language}}) {
      edges {
        node {
          ns
          data
          language
        }
      }
    }
  }
`;
